* {
  margin: 0;
  padding: 0;
}

*, :before, :after {
  box-sizing: border-box;
}

html, body {
  background-color: #bfb7b7;
  width: 100%;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-size: 100%;
  line-height: 1;
}

a {
  text-decoration: none;
}

a:visited {
  color: #000;
  text-decoration: none;
}

a:focus, a:active {
  outline: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

body {
  align-items: center;
  display: flex;
}

.header {
  background-color: #fff;
  width: 100%;
}

.container {
  justify-content: space-between;
  align-items: stretch;
  margin: 0 50px;
  display: flex;
}

.logo {
  justify-content: center;
  align-items: stretch;
  display: flex;
}

.logo-link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  justify-content: space-between;
  align-items: stretch;
  height: 60px;
  display: flex;
}

.nav__list li {
  margin: 0 10px;
  display: flex;
}

.nav__list li:last-child {
  margin: 0 0 0 10px;
}

.nav__list li:first-child {
  margin: 0 10px 0 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  justify-content: baseline;
  color: #00acdc;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
  display: flex;
  position: relative;
}

.nav__link.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  bottom: 0;
}

